import React from 'react';
import './style.scss';
import { StaticQuery, graphql } from "gatsby"
import { FaArrowCircleRight } from 'react-icons/fa';

export default () => (
  <StaticQuery
  query={graphql`
  {
    allGoogleSpreadsheetCandidates(sort: {fields: candidateId, order: ASC}) {
      edges {
        node {
          candidateId
          candidateInfo
          candidateMug
          candidateName
        }
      }
    }
  }
`}

render={data => (
<div>
<section class="section">
<div class="container">
<h2 class="title is-1 has-rule has-text-centered">The Candidates</h2>
      <hr class="level has-text-centered"/>
      <h3 class="subtitle is-4 has-text-centered">Click a candidate to see where they stand.</h3>
      <hr class="level has-text-centered"/>
<p>&nbsp;</p>
</div>

<div class="container">
    <div class="columns is-centered">
        <div class="column is-four-fifths">
        <div class="columns is-multiline is-mobile is-centered">
          {/* CARDS */}
          {data.allGoogleSpreadsheetCandidates.edges.map(({ node }, index) => (    
  <div class="column is-one-quarter-desktop is-one-third-tablet is-half-mobile">
  <div class="card">
  <div class="card-image card-round">
    <figure class="image">
      <a href={node.candidateSlug}><img class="is-rounded" src={node.candidateMug} alt={node.candidateId}/></a>
    </figure>
    <figure class="image">
      <a href={node.candidateSlug}><img class="is-rounded" src="../images/candidate-mugs/buttigieg.jpg" alt={node.candidateId}/></a>
    </figure>
  </div>
  <div class="card-content">
  <p class="title is-4"><a href={node.candidateSlug}>{node.candidateName}</a></p>
  <p class="subtitle is-6">{node.candidateInfo}<a href={node.candidateSlug}>&nbsp;&nbsp;<FaArrowCircleRight class="has-text-danger"/></a></p>
  </div>
</div>
  </div>
  ))}
  {/* END CARDS */}
  </div></div></div>
  </div>
  </section>
</div> )}
/>
);